import React, { Component } from "react";
import PropTypes from "prop-types";

import SelectBox, { SelectBoxOption } from "@xero/xui/react/select-box";

const options = [
  { key: "Profdev", label: "Professional development" },
  { key: "Wellbeing", label: "Wellbeing" },
  { key: "Presenting", label: "Presenting" },
  { key: "Social", label: "Social" },
  { key: "Volunteer", label: "Volunteer" }
];

export class TypeFilter extends Component {
  isOptionSelected = key => this.props.selectedTypes.includes(key);

  handleTypeChange = key => {
    if (this.isOptionSelected(key)) {
      this.props.removeType(key);
      return;
    }

    this.props.addType(key);
  };

  render = () => (
    <SelectBox
      inputGroupClasses={this.props.className}
      labelText="Select Type"
      buttonContent={
        this.props.selectedTypes
          .map(key => options.find(option => option.key === key).label)
          .join(", ") || "Select Type"
      }
      closeAfterSelection={false}
      onSelect={this.handleTypeChange}
      islabelHidden
    >
      {options.map(({ key, label }) => (
        <SelectBoxOption
          key={key}
          value={key}
          id={key}
          isSelected={this.isOptionSelected(key)}
          showCheckboxes
        >
          {label}
        </SelectBoxOption>
      ))}
    </SelectBox>
  );
}

TypeFilter.propTypes = {
  selectedTypes: PropTypes.arrayOf(PropTypes.string).isRequired,
  addType: PropTypes.func.isRequired,
  removeType: PropTypes.func.isRequired
};

export default TypeFilter;
