import React, { Component } from "react";
import PropTypes from "prop-types";

import SelectBox, { SelectBoxOption } from "@xero/xui/react/select-box";

const options = [
  { key: "AKL", label: "Auckland" },
  { key: "WLG", label: "Wellington" }
];

export class CityFilter extends Component {
  isOptionSelected = key => this.props.selectedCities.includes(key);

  handleCityChange = key => {
    if (this.isOptionSelected(key)) {
      this.props.removeCity(key);
      return;
    }

    this.props.addCity(key);
  };

  render = () => (
    <SelectBox
      inputGroupClasses={this.props.className}
      labelText="Select city"
      buttonContent={
        this.props.selectedCities
          .map(key => options.find(option => option.key === key).label)
          .join(", ") || "Select city"
      }
      closeAfterSelection={false}
      onSelect={this.handleCityChange}
      islabelHidden
    >
      {options.map(({ key, label }) => (
        <SelectBoxOption
          key={key}
          value={key}
          id={key}
          isSelected={this.isOptionSelected(key)}
          showCheckboxes
        >
          {label}
        </SelectBoxOption>
      ))}
    </SelectBox>
  );
}

CityFilter.propTypes = {
  selectedCities: PropTypes.arrayOf(PropTypes.string).isRequired,
  addCity: PropTypes.func.isRequired,
  removeCity: PropTypes.func.isRequired
};

export default CityFilter;
