import React, { Component, Fragment } from "react";
import { intlShape, injectIntl } from "react-intl";

import { XUIActions } from "@xero/xui/react/structural";
import XUIButton from "@xero/xui/react/button";
import { XUIPageHeader } from "@xero/xui/react/structural";

import headerHelper from "../helpers/xero-header";
import ActivityCard from "./Activity/ActivityCard";
import Filter from "./Filter";

import { activities } from "../api/db";

class HomePage extends Component {
  state = {
    activities
  };

  componentDidMount() {
    headerHelper(this.props.history, "home");
  }

  onFilterChange = query => {
    console.log(query);

    this.setState({
      activities: activities.filter(
        activity =>
          (query.cities.length === 0 ||
            activity.cities.some(city => query.cities.includes(city))) &&
          (query.time.length === 0 ||
            activity.time.some(time => query.time.includes(time))) &&
          (query.locations.length === 0 ||
            activity.locations.some(location =>
              query.locations.includes(location)
            )) &&
          (query.types.length === 0 ||
            activity.types.some(type => query.types.includes(type)))
      )
    });
  };

  render() {
    const { activities } = this.state;

    return (
      <Fragment>
        <XUIPageHeader
          title="XPlore activities you can get involved in at XERO"
          contentClassName="xui-page-width-standard"
          actions={
            <XUIActions
              primaryAction={
                <XUIButton
                  key="one"
                  size="small"
                  isLink
                  type="button"
                  variant="standard"
                  href="https://docs.google.com/forms/d/e/1FAIpQLSeVXJT3MYp3AxHss2XB29UvlmKQ15nxgd3yjnh84ClXZADW3g/viewform?usp=sf_link"
                >
                  Add new/edit opportunity
                </XUIButton>
              }
              secondaryAction={
                <XUIButton
                  key="two"
                  size="small"
                  isLink
                  type="button"
                  variant="standard"
                  href="https://docs.google.com/forms/d/e/1FAIpQLSeJVqYsL0SJUp1124qnPBRuHZmnpfRkFVfkc1SFt0wN6Pyr-g/viewform?usp=sf_link"
                >
                  Give feedback
                </XUIButton>
              }
            />
          }
        />
        <XUIPageHeader
          contentClassName="xui-page-width-standard"
          style={{ borderTop: "1px solid #ccced2" }}
        >
          <div className="xui-padding-small">
            Sherlock will direct you to the appropriate page or Slack channel
            where you can learn more about getting involved in these activities.
            If directed to a person, let them know what youâ€™re interested in!
          </div>
        </XUIPageHeader>
        <div className="xui-page-width-standard xui-margin-vertical-xlarge">
          <Filter onChange={this.onFilterChange} />
          <div className="xui-row-grid">
            {activities.map(({ id, ...props }) => (
              <div
                className="xui-column-3-of-12-wide xui-column-4-of-12-medium xui-column-12-of-12 xui-padding-bottom-large"
                key={id}
              >
                <ActivityCard {...props} />
              </div>
            ))}
          </div>
        </div>
      </Fragment>
    );
  }
}

HomePage.propTypes = {
  intl: intlShape.isRequired
};

export default injectIntl(HomePage);
