import React, { Component } from "react";
import PropTypes from "prop-types";

import SelectBox, { SelectBoxOption } from "@xero/xui/react/select-box";

const options = [
  { key: "Virtual", label: "Virtual" },
  { key: "Inoffice", label: "In office" },
  { key: "Offsite", label: "Offsite" }
];

export class LocationFilter extends Component {
  isOptionSelected = key => this.props.selectedLocations.includes(key);

  handleLocationChange = key => {
    if (this.isOptionSelected(key)) {
      this.props.removeLocation(key);
      return;
    }

    this.props.addLocation(key);
  };

  render = () => (
    <SelectBox
      inputGroupClasses={this.props.className}
      labelText="Select location"
      buttonContent={
        this.props.selectedLocations
          .map(key => options.find(option => option.key === key).label)
          .join(", ") || "Select location"
      }
      closeAfterSelection={false}
      onSelect={this.handleLocationChange}
      islabelHidden
    >
      {options.map(({ key, label }) => (
        <SelectBoxOption
          key={key}
          value={key}
          id={key}
          isSelected={this.isOptionSelected(key)}
          showCheckboxes
        >
          {label}
        </SelectBoxOption>
      ))}
    </SelectBox>
  );
}

LocationFilter.propTypes = {
  selectedLocations: PropTypes.arrayOf(PropTypes.string).isRequired,
  addLocation: PropTypes.func.isRequired,
  removeLocation: PropTypes.func.isRequired
};

export default LocationFilter;
