import React, { Component } from "react";
import PropTypes from "prop-types";

import CityFilter from "./CityFilter";
import TimeFilter from "./TimeFilter";
import LocationFilter from "./LocationFilter";
import TypeFilter from "./TypeFilter";

export class Filter extends Component {
  state = {
    cities: [],
    time: [],
    locations: [],
    types: []
  };

  componentDidUpdate(prevProps, prevState) {
    if (prevState !== this.state) {
      this.props.onChange(this.state);
    }
  }

  addCity = key => {
    this.setState(state => {
      return { cities: state.cities.concat(key) };
    });
  };

  removeCity = key => {
    this.setState(state => {
      return { cities: state.cities.filter(city => city !== key) };
    });
  };

  addTime = key => {
    this.setState(state => {
      return { time: state.time.concat(key) };
    });
  };

  removeTime = key => {
    this.setState(state => {
      return { time: state.time.filter(city => city !== key) };
    });
  };

  addType = key => {
    this.setState(state => {
      return { types: state.types.concat(key) };
    });
  };

  removeType = key => {
    this.setState(state => {
      return { types: state.types.filter(type => type !== key) };
    });
  };

  addLocation = key => {
    this.setState(state => {
      return { locations: state.locations.concat(key) };
    });
  };

  removeLocation = key => {
    this.setState(state => {
      return {
        locations: state.locations.filter(location => location !== key)
      };
    });
  };

  render = () => (
    <div className="xui-row-grid xui-margin-bottom">
      <div className="xui-column-3-of-12-wide xui-column-4-of-12-medium xui-column-12-of-12 xui-padding-bottom-small">
        <CityFilter
          selectedCities={this.state.cities}
          addCity={this.addCity}
          removeCity={this.removeCity}
        />
      </div>
      <div className="xui-column-3-of-12-wide xui-column-4-of-12-medium xui-column-12-of-12 xui-padding-bottom-small">
        <TimeFilter
          selectedTimes={this.state.time}
          addTime={this.addTime}
          removeTime={this.removeTime}
        />
      </div>
      <div className="xui-column-3-of-12-wide xui-column-4-of-12-medium xui-column-12-of-12 xui-padding-bottom-small">
        <TypeFilter
          selectedTypes={this.state.types}
          addType={this.addType}
          removeType={this.removeType}
        />
      </div>
      <div className="xui-column-3-of-12-wide xui-column-4-of-12-medium xui-column-12-of-12 xui-padding-bottom-small">
        <LocationFilter
          selectedLocations={this.state.locations}
          addLocation={this.addLocation}
          removeLocation={this.removeLocation}
        />
      </div>
    </div>
  );
}

Filter.propTypes = {
  onChange: PropTypes.func.isRequired
};

export default Filter;
