import React, { Component } from "react";
import PropTypes from "prop-types";
import "./ActivityCard.css";

import { XUIPanel, XUIPanelSection } from "@xero/xui/react/structural";

export class ActivityCard extends Component {
  render() {
    return (
      <XUIPanel>
        <XUIPanelSection>
          <a className="xui-text-decoration-none" href={this.props.link} target="blank"
    			rel="noopener noreferrer">
            <div className="xui-padding-small activity-card">
              <img
                className="activity-card-image"
                src={`assets/${this.props.imagePath}`}
                alt="activity"
              />
              <div className="xui-text-label xui-text-align-center activity-card-title xui-padding-top-small">
                {this.props.name}
              </div>
            </div>
          </a>
        </XUIPanelSection>
      </XUIPanel>
    );
  }
}

ActivityCard.propTypes = {
  name: PropTypes.string.isRequired,
  imagePath: PropTypes.string,
  link: PropTypes.string,
};

export default ActivityCard;
