import React, { Component } from "react";
import PropTypes from "prop-types";

import SelectBox, { SelectBoxOption } from "@xero/xui/react/select-box";

const options = [
  { key: "Business", label: "Office hours" },
  { key: "After", label: "After hours" }
];

export class TimeFilter extends Component {
  isOptionSelected = key => this.props.selectedTimes.includes(key);

  handleTimeChange = key => {
    if (this.isOptionSelected(key)) {
      this.props.removeTime(key);
      return;
    }

    this.props.addTime(key);
  };

  render = () => (
    <SelectBox
      inputGroupClasses={this.props.className}
      labelText="Select Times"
      buttonContent={
        this.props.selectedTimes
          .map(key => options.find(option => option.key === key).label)
          .join(", ") || "Select Times"
      }
      closeAfterSelection={false}
      onSelect={this.handleTimeChange}
      islabelHidden
    >
      {options.map(({ key, label }) => (
        <SelectBoxOption
          key={key}
          value={key}
          id={key}
          isSelected={this.isOptionSelected(key)}
          showCheckboxes
        >
          {label}
        </SelectBoxOption>
      ))}
    </SelectBox>
  );
}

TimeFilter.propTypes = {
  selectedTimes: PropTypes.arrayOf(PropTypes.string).isRequired,
  addTime: PropTypes.func.isRequired,
  removeTime: PropTypes.func.isRequired
};

export default TimeFilter;
