const NOOP = () => {};

const XERO = window.XERO || {
  Header: {
    updateNavigation: NOOP
  },
  bus: {
    on: NOOP
  }
};

export default function(history, newNavId) {
  XERO && XERO.Header && XERO.Header.updateNavigation && XERO.Header.updateNavigation(newNavId);
  XERO.bus.on("header:routedNavigation", (url, selectedNavId) => {
    history.push(url);
  });
}